<template>
  <PageContainer :title="$t('forget_password')">
    <form
      v-if="defaultKey === 'form'"
      class="w-full max-w-screen-sm mx-auto"
      @submit.prevent="handleSubmit"
    >
      <FormInput
        :placeholder="$t('auth_input_email')"
        :error="emailError"
        :value="email"
        @onChange="handleChangeEmail"
      />
      <button
        class="rounded w-full py-2.5 lg:py-4 text-center text-white bg-button hover:bg-main-red text-xl lg:text-3xl"
      >
        {{ $t("auth_submit") }}
      </button>
    </form>
    <div class="flex justify-center" v-else-if="defaultKey === 'confirm'">
      <div class="flex items-center gap-8 lg:text-4xl flex-col lg:flex-row">
        <img src="@/assets/mail.png" class="w-20 h-20" />
        {{ $t("forget_password_email") }}
        <br />
        {{ $t("forget_password_validate") }}
      </div>
    </div>
  </PageContainer>
</template>
<script>
import PageContainer from "@/components/PageContainer";
import FormInput from "@/components/FormInput";

export default {
  components: {
    PageContainer,
    FormInput,
  },
  data() {
    return {
      defaultKey: "form",
      email: "",
      emailError: "",
    };
  },
  methods: {
    handleChangeEmail(value) {
      this.email = value;
    },
    handleSubmit() {
      if (!this.email) {
        this.emailError = `*${this.$t("error_email")}`;
        return;
      }
      this.defaultKey = "confirm";
    },
  },
};
</script>
